.Categories {

	h1 {
		font-weight: 450;
	}

	display: flex;
	flex-direction: column;
	// height: 0vh;
	min-height: 60vh;
	// row-gap:.2cm;
	align-items: center;
}

.categories-head {
	font-size: 35px;
	font-weight: 500;

	font-family: Bree Serif;
	// border-right: .15em solid orange;
	// letter-spacing: .07cm;
	// line-height: 1.2cm;
	// color: blueviolet;
	color: rgb(96, 96, 96);
	overflow: hidden;
	white-space: nowrap;
	// animation: typing 4s steps(40, end);

}

@keyframes typing {
	0% {
		width: 0
	}

	15% {
		width: 15%;
	}

	30% {
		width: 30%;
	}

	45% {
		width: 45%;
	}

	60% {
		width: 60%;
	}

	75% {
		width: 75%;
	}

	90% {
		width: 90%;
	}

	100% {
		width: 100%
	}
}

.choose {
	font-size: 20px;
	font-family: inika;
}
 
.categories-container {

	display: flex;
	
	flex-wrap: wrap;

	justify-content: center;
	
	
	align-items: flex-start;
	column-gap: .6cm;
	row-gap: .6cm;

}

.category {
	width: auto;
	height: auto;
	display: flex;
	justify-content: center;
	
	background-color: #d8affc;
	border-radius: 5px;
	
	cursor: pointer;
	font-weight: 600;

    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;

	
	
}

.category:hover {
	background-color: #cb98f6;


}
 
.cat-img {
	width: 5cm;
	height: 5cm;
	transition: .3s;
}

.category-content {
	padding: 1vh 3vh 1vh 3vh;
	

	h1 {
		font-size: 25px;
		margin: 0px 0px;
		// font-weight: 100/	;
		transition: .3s;
		font-weight: 400;
		font-family: bree serif;
		// line-height: .55cm;
		// color: #2c3941;
		color: #38383d;
		// letter-spacing: .03cm;
	}

	p {
		font-weight: 500;
	}

}

.category-link {
	text-decoration: none;
	color: whitesmoke;
}

