.start-here {

	animation: learn 1.5s ease infinite;
}

@keyframes opac {
	0% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(-5px);
	}

	100% {
		transform: translateY(0);
	}
}

@keyframes opac1 {
	0% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(-10px);
	}

	100% {
		transform: translateY(0);
	}
}

@keyframes dict {
	0% {
		color: blueviolet;
	}

	50% {
		color: #6e32aa;
	}

	// 60%  { color: #6e32aa; }
	100% {
		color: blueviolet;
	}


}

@keyframes learn1 {
	0% {
		background-color: #228b22;
	}

	50% {
		background-color: #40590e;
	}

	// 60%  { background-color: #6e32aa; }
	100% {
		background-color: #228b22;
	}


}

@keyframes learn {
	0% {
		transform: scale(1);
	}

	50% {
		transform: scale(1.05);
	}

	// 60%  { transform: scale(1.1); }
	100% {
		transform: scale(1);
	}


}

@keyframes feel {
	0% {
		filter: brightness(100%);

	}

	50% {
		filter: brightness(80%);

	}

	// 60%  { color: #6e32aa; }
	100% {
		filter: brightness(100%);

	}


}


