.SignUp {
    min-height: 70vh;
    display: flex;
    justify-content: center;
    // padding-top: 10vh;
}

.signup-container {
    // padding-top: 10vh;
    display: flex;
    // flex-direction: column;
    justify-content: center;
    align-items: center;
    column-gap: 2cm;
}

.signin-box {
    width: 10cm;
    height: auto;
    // border: 1px solid black;
    // background-color: #ABC9FF;
    background-color: #ff97cf;

    // box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;

    border-radius: 10px;
    padding: 15px;
}

.login-p {
    font-size: 25px;
    font-family: raleway;
    font-weight: 600;
}

.why-signup {
    text-align: left;
}

.coll {
    font-weight: 600;
}

.sign-g {
    cursor: pointer;
    // font-family: lora;
    font-size: 16px;
    // color: #28282b;
    border-radius: 3px;
    width: fit-content;
    font-weight: 500;
    color: black;
    padding: 10px;
    background-color: rgb(234, 230, 230);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;




}

.user-profile-img {
    border-radius: 50px;
    // width: 5cm;
    border: 2px solid #28282b;
    margin-left: .5cm;
}
.sub-search{
    color: rgb(84, 83, 83);
}
.create {
    font-size: 35px;
    font-family: bree serif;
    color: rgb(70, 69, 69);
    // overflow: hidden;
    // white-space: nowrap;

    // animation: typing 4s steps(40, end);
    // font-weight: 600;
    // padding: 10px;

}

.welcome-user {
    font-size: 30px;
    font-family: raleway;
    color: #28282b;

    font-weight: 600;
}

.profile-data {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: .3cm;
    background-color: #87f195;
    // background-color: #FFBBCC;
    width: auto;
    padding: .3cm 0cm .3cm 0cm;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    border-radius: 10px;
    // border: 2px solid #28282b;
}

.st {
    border: none;
    border-radius: 5px;
    font-size: 14px;
    background-color: rgb(228, 69, 69);
    color: whitesmoke;
    cursor: pointer;
    padding: 3px 5px 3px 5px;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

}

.st:hover {
    box-shadow: rgba(2, 0, 0, 0.978) 0px 0px 0px 1.5px;

}

.subs {
    font-size: 14px;
    font-weight: 600;
}

.name-section {
    display: flex;
    flex-direction: column;
    row-gap: .2cm;
    padding-right: .5cm;
}
.login-with-google-btn {
    // transition: background-color .3s, box-shadow .3s;
    cursor: pointer;
    padding: 12px 16px 12px 42px;
    border: none;
    border-radius: 3px;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;


    color: #313030;
    font-size: 14px;
    font-weight: 500;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    
    background-image: url(data:image/svg+xml;base64,);
    background-color: white;
    background-repeat: no-repeat;
    background-position: 12px 12px;
    
    display: flex;
    justify-content: center;
    align-items: center;
    
    &:active {
      background-color: #eeeeee;
    }
    
    // &:focus {
    //   outline: none;
    //   box-shadow: 
    //     0 -1px 0 rgba(0, 0, 0, .04),
    //     0 2px 4px rgba(0, 0, 0, .25),
    //     0 0 0 1px #5283de;
    // }
    
    &:disabled {
      filter: grayscale(100%);
      background-color: #ebebeb;
      box-shadow: 0 -1px 0 rgba(0, 0, 0, .04), 0 1px 1px rgba(0, 0, 0, .25);
      cursor: not-allowed;
    }
  }

  .login-with-google-btn:hover{
    background-color: rgb(243, 242, 242);
  }
  
  
@keyframes typing {
    0% {
        width: 0
    }

    15% {
        width: 15%;
    }

    30% {
        width: 30%;
    }

    45% {
        width: 45%;
    }

    60% {
        width: 60%;
    }

    75% {
        width: 75%;
    }

    90% {
        width: 90%;
    }

    100% {
        width: 100%
    }
}

.check-mark {
    width: .6cm;
    height: .6cm;
}

.google-s {
    width: 1cm;
}

.benefit {
    display: flex;
    column-gap: .2cm;
    align-items: center;

}

.benefit-content {
    font-size: 22px;
    font-family: itim;
}

button:disabled,
button[disabled] {
    opacity: .6;
}

.checkbox-section {
    display: flex;
    align-items: baseline;
    column-gap: .1cm;
    padding-top: .5cm;
    text-align: center;
}
@media screen and (min-width:1700px){
    .login-with-google-btn{
        font-size: 16px;
        background-position: 12px 15px;
    }
}
@media screen and (max-width:1200px) {
    .signup-container {
        // padding-top: 5vh;
        flex-direction: column-reverse;
        align-items: center;
        row-gap: 1cm;

    }
}

@media screen and (max-width:1000px) {
    .create {
        font-size: 30px;
    }

}

@media screen and (max-width:800px) {
    .create {
        font-size: 25px;
    }

    .benefit-content {
        font-size: 20px;
    }

    .user-profile-img {
        width: 2cm;
    }

}

@media screen and (max-width:600px) {

    .welcome-user {
        font-size: 25px;
    }

    .benefit-content {
        font-size: 18px;
    }

    .check-mark {
        width: .4cm;
        height: .4cm;
    }

    .benefit {
        column-gap: .1cm;

    }

    // .profile-data{
    //     width: 7cm;
    // }
    .sign-g{
        font-size: 15px;
    }

}

@media screen and (max-width:500px) {

    .welcome-user {
        font-size: 22px;
    }

    // .profile-data{
    //     width: 90vw;
    // }
    .signin-box {
        width: 75vw;
    }

    .subs {
        font-weight: 500;
    }
    .login-with-google-btn{
        font-size: 14px;
    }

    .sign-g{
        font-size: 14px;
    }

}

@media screen and (max-width:400px) {
    .benefit-content {
        font-size: 16px;
    }

    .user-profile-img {
        width: 1.5cm;
    }

    .user-profile-img {
        width: 1.5cm;
    }

    // .profile-data{
    //     width: 5.7cm;
    // }
    .st {
        font-size: 12px;
    }

    .create {
        font-size: 20px;
    }
    

}

@media screen and (max-width:300px) {
    .create {
        font-size: 18px;
    }

    .welcome-user {
        font-size: 20px;
    }

    // .profile-data{
    //     width: 5.4cm;
    // }
    .login-with-google-btn{
        font-size: 13px;
    }
