.contact-container{
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 67vh;
	// border: 1px solid black;
	
}
.address{
	// text-align: left;
	padding: 0cm .3cm;
	width: 10cm;
}
.submit-contact{
	width: 100%;
	padding: .25cm;
}
.add-det{
	font-size: 14px;
	font-weight: 600;
	color: rgb(97, 97, 97);
}
.coe{
	color: blue;
	cursor: pointer;
}
.coe:hover{
	text-decoration: underline;
}
.Contact{
	display: flex;
	flex-direction: column;
	align-items: center;
    
	
	width: fit-content;
	padding: .5cm .2cm;
	background-color: transparent	;
    // background-color: rgb(251,220,168,73);

	

	border-radius: 3px;
	border: thin solid rgb(175, 175, 175);
	// box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;


}

.contact-form{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	row-gap:0.5cm;
	// border: 1px solid black;
	// width: 20vw;
	padding: 2vh 0vw 2vh 0vw;

}

.contact-inputs{
	background-color: whitesmoke;
	border: none;
	width: 9cm;
	height: auto;
	font-size: 16px;
	padding: 10px;
	font-family: roboto;
	// font-weight: 500;
	border-radius: 5px;
	// box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;


}
.submit-form{
	background-color: #77dd77;
	height: 1cm;
	display: flex;
	justify-content: center;
	// align-items: center;
	border: none;
	border-radius: 5px;
	padding-left: 10px;
	padding-right: 10px;
	cursor: pointer;
	font-size: 30px;
	font-family: Concert One;
	box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}


.social-icons{
	display: flex;
	column-gap: .3cm;
	// padding-bottom: 10vh;
}
.icon{
	width: .8cm;
	height: .8cm;
}

