@media screen and (max-width:900){
	.icon{
		width: 1cm;
		height: 1cm;
	}
}
@media screen and (max-width: 700px){
	.contact-inputs{
		width: 8cm;
	}
}
@media screen and (max-width: 500px){
	// .contact-form{
	// 	// row-gap:.3cm;
	// }
	.contact-inputs{
		width: 70vw;
		font-size: 16px;
	}
	.submit-form{
	font-size: 25px;
	height: .9cm;
	}
	.icon{
		width: .9cm;
		height: .9cm;
	}
	.Contact{
		width: 80vw;
	}
	.add-det{
		font-size: 13px;
		font-weight: 500;
	}
	.address{
		width: 75vw;
	}
	
}
@media screen and (max-width: 450px){
	.contact-inputs{
		
		font-size: 15px;
	}
	.submit-form{
	font-size: 23.5px;
	height: .9cm;
	}
	
	
}
@media screen and (max-width: 400px){
	.contact-inputs{
		
		font-size: 14px;
	}
	.submit-form{
	font-size: 22px;
}

	
}
@media screen and (max-width: 300px){
	.contact-inputs{
		width: 70vw;
		font-size: 15px;
	}
	.submit-form{
	font-size: 20px;
	height: .7cm;
}
.icon{
	width: .8cm;
	height: .8cm;
}
.add-det{
	font-size: 12px;
}
	
}
@media screen and (max-width: 250px){
	.contact-inputs{
		
		font-size: 13px;
	}
	.submit-form{
	font-size: 16px;
	height: .6cm;
}

	
