@import url('https://fonts.googleapis.com/css?family=Amita|Handlee|Labrada|Wallpoet|VT323|Ubuntu|Julee|Lora|Kalam|Merienda');
.Organic{
  display: flex;
  
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}


.searchbox{
  // position: relative;
  width: 9cm;
  height:.6cm;
  border-radius: 5px;
  font-size: 22px;
  padding: 10px;
  /*  word-break: break-all;*/
  /*  background-color: #1f1f1f;*/
  background-color: whitesmoke;
  border: none;
  font-family: roboto;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

  // font-weight: 600;

}
.reactions{
  width: 80vw;
  display: flex;
  flex-wrap: wrap;
  column-gap: .4cm;
  row-gap: .4cm;
  justify-content: center;
  padding : 2vh 5vw 0vh 5vw;
 
}
input:focus{
  outline:none;

}
.header-organic{
  display: flex;
}
.filteredRxns{
  width: auto;
  height: auto;
  max-width: 80vw;
  border-radius: 5px;
  // background-color: #d9c6ee;
  background-color: #F3F8FF;
  
  cursor: pointer;
  // transition: .1s;
  
  // transition: .1s;
  
 
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  
}
.chc{
  background-color: #ACECA1;
}
.filteredRxns:hover{  
  
  
  // border: 2px solid #28282b;
  box-shadow: rgba(2, 0, 0, 0.978) 0px 0px 0px 2px;
}

.rxn-name{
  padding:8px 15px 8px 15px ;
  font-size: 20px;
  font-family: roboto;
  color: #000000;
  font-weight: 500; 
}

.rxn-not-found{
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: .5cm;
}
.rxn-no-img{
  width: 5.5cm;
  padding-top: 1vh;
}

.rxn-no-text{
  color: gray;
  font-size: 15px;
}
