.mail-us {
	width: auto;
	padding: 2px 10px;

	height: 1.2cm;
	font-size: 30px;
	font-family: Inika;
	background-color: #77dd77;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 10px;

	box-shadow:
		/* offset-x | offset-y | blur-radius | spread-radius | color */
		0px 24px 38px 3px hsla(0, 0%, 0%, 0.14),
		0px 9px 46px 8px hsla(0, 0%, 0%, 0.12),
		0px 11px 15px -7px hsla(0, 0%, 0%, 0.2);
	// width: 4cm;
}

@media screen and (max-width: 1250px) {
	

	
	.invest-img {
		width: 7cm;
		padding-top: 5vh;
	}
}

@media screen and (max-width: 1050px) {
	.mail-us {
		font-size: 25px;
		height: 1cm;
	}

	.invest-container::before {

		background-size: 9cm 8cm;
	}
}

@media screen and (max-width: 700px) {
	.mail-us {
		font-size: 22px;
		height: .8cm;
	}

}

@media screen and (max-width: 600px) {

	.prob-desc {
		width: 80vw;
	}

	.Invest{
		align-items: center;
	}

}

@media screen and (max-width: 500px) {

	.invest-img {
		width: 5cm;
		padding-top: 5vh;
	}

	.invest-container {
		width: 80vw;
	}
}

@media screen and (max-width:400px) {
	.invest-container::before {

		background-size: 7cm 6cm;
	}

	.invest-container {
		width: 75vw;
	}
}

@media screen and (max-width: 300px) {

	.invest-img {
		width: 3cm;
		padding-top: 5vh;
	}

	.invest-container::before {

		background-size: 5cm 4cm;
	}
}